import { Controller } from '@ali/4ever-cangjie';
import { SelectManager } from '../../utils/SelectManager';

export function createOnCangjieFocus(manager: SelectManager) {
  return (controller: Controller, next: any) => {
    const { isSelecting } = manager;
    if (isSelecting) {
      return controller;
    }
    return next();
  };
}
